@import '../../style/tools/mixins';

.personal-info {
    display: flex;
    flex-direction: column;

    @include desktop() {
        justify-content: flex-start; }

    @include desktopBig() {
        justify-content: flex-start; }

    @include mobile() {
        > h1 {
            margin-top: 0;
            text-align: center; }

        > .info-list > li {
            text-align: center; } }

    > .name {
        display: inline-flex;
        flex-direction: column;
        letter-spacing: .75px;

        &::after {
            content: "computer scientist";
            display: inline-block;
            font-size: 16px;
            text-transform: capitalize; } }

    > .info-list {
        list-style-type: none;
        margin: 0;
        padding: 0px;

        > li {
            line-height: 24px;
            text-transform: capitalize;

            > p > i {
                margin: 0px 8px; } } } }
