@import '../../style/settings/variables';
@import '../../style/tools/mixins';

.frame-wraper {
  align-items: flex-start;

  @include desktop() {
    display: flex;
    height: 100vh;
    margin-top: 10vh;
    position: fixed;
    right: 80px; }

  @include desktopBig() {
    display: flex;
    height: 100vh;
    margin-top: 10vh;
    position: fixed;
    right: 80px; }

  @include mobile() {
    display: flex;
    justify-content: center;
    margin: 32px 0px;
    grid-row: 3; } }


.app {
  align-items: flex-start;
  color: $second;
  display: flex;
  justify-content: center;
  width: 100%;

  @include desktop() {
    grid-column: 1;
    grid-row: 2; }

  @include desktopBig() {
    grid-column: 1;
    grid-row: 2; }

  @include mobile() {
    grid-row: 2; }

  > .content {
    display: flex;
    flex-direction: column;

    @include desktop() {
      flex: 0 1 60%;
      justify-content: flex-start;
      padding-left: 28px; }

    @include mobile() {
      align-items: center;
      flex: 1 1;
      justify-content: center; }

    > .line {
      background-color: lighten($primary, 70%);
      border-radius: 4px;
      height: 2px;
      margin: 24px 0px;
      width: 100%; }

    > p {
      font-size: 24px;
      margin: 0 auto 16px; } } }
