@import '../../style/settings/variables';
@import '../../style/tools/mixins';

.frame {
    border: 8px solid $second;
    padding: 1px;
    box-sizing: border-box;

    @include desktop() {
        width: 350px; }

    @include desktopBig() {
        width: 400px; }

    @include mobile() {
        width: 320px;
        max-height: 480px; } }
